import {types} from '../types/types'
const initialState ={
    thours:[]
}

export default function (state=initialState, action) {
    switch (action.type) {

        case types.agregar:
            return {
                ...state, 
                thours:[...state.thours,action.payload]
            }
        
            case types.borrar:
                return{
                    ...state,
                    thours:state.thours.filter(thour => thour.id !== action.payload)
                }
        
        default:
            return state;
    }
}