import React from 'react'

const VerCardExcursiones = ({item ,setstate}) => {
  
    return (
        <>
        <div className="card m-4" >
<img src={item.img} className="card-img-top" alt="..."/>
<div className="card-body">
<h5 className="card-title">{item.title}</h5>
<p className="card-text">{item.description.substring(0, 100)}...</p>

<button type="button" className="btn btn btn-danger" data-bs-toggle="modal" data-bs-target="#exampleModal" data-bs-whatever="@mdo" onClick={ () =>setstate(item.id)}>Ver m√°s</button>
</div>
</div>
    </>
    )
}

export default VerCardExcursiones
