import React from 'react'
import {useSelector, useDispatch} from 'react-redux'
import {BorrarActionThour} from '../actions/ActionThour'

const ListarExcursiones = () => {
    const dispatch = useDispatch()

    const agenda = useSelector(state => state.thours)
    
    return (
        <div className="card mt-5">
        <div className="card-body">
            <h2 className="card-title text-center">Agenda</h2>
            <div className="lista-citas">
                <table className="table">
                    <thead>
                        <tr>
                            <th scope="col">Nombre</th>
                            <th scope="col">Fecha</th>
                            <th scope="col">Precio</th>
                            <th scope="col">Acci√≥n</th>
                        </tr>
                    </thead>
                    <tbody>
                        {agenda.thours.map(thour => (
                        <tr key={thour.id}>
                            <td>{thour.nombre}</td>
                            <td>{thour.fecha}</td>
                            <td>${thour.factura}</td>
                            <td><button className="btn btn-danger"
                            onClick={() => 
                                dispatch(BorrarActionThour(thour.id))
                            }
                            >
                                Borrar &times;
                               </button>
                            </td>
                        </tr>
                         ) )}
                    </tbody>
                </table>
            </div>
        </div >
    </div >
    )
}

export default ListarExcursiones
