import React,{useState} from 'react'
import {excursiones} from '../db'
import VerCardExcursiones from '../pages/VerCardExcursiones'
import VerModalExcursion from '../pages/VerModalExcursion'
import ValidarAccion from '../actions/ValidarAccion'
//ACCIONES
import {actionThour} from '../actions/ActionThour'
import {useDispatch,useSelector} from 'react-redux'

const Excursiones = () => {
  const [state, setstate] = useState(1)
   const dispatch = useDispatch()  

   const AgregarThour = (thour) => {
    dispatch(actionThour(thour))
}

const ValidarFechaReducer = (estado) => {
  dispatch(ValidarAccion(estado))
}

const error = useSelector(estado => estado.error)

    return (
        <>
        
       { error.error  ? <div className="alert alert-danger text-center p2">El Thour no se guard√≥. Debes agregar una fecha.</div> : null}
         
        <div className="overflow-auto card" style={{height: "600px"}}>
      {
          excursiones.map(item => (
              <VerCardExcursiones item={item} setstate={setstate}  key={item.id}/>
              ))
            }
         </div>

       <VerModalExcursion AgregarThour={AgregarThour} state={state} error={error} ValidarFechaReducer={ValidarFechaReducer}/>     

  
  </>
    )
}

export default Excursiones
