export const obtenerStateStorage = () => {
    const thoursStorage = localStorage.getItem('thours');
    if(thoursStorage === null) {
        return undefined
    }
    return JSON.parse(thoursStorage);
}

export const guardarStateStorage = state => {
    const thoursState = JSON.stringify(state);
    localStorage.setItem('thours', thoursState);
}