import React,{useState} from 'react'
import { excursiones } from '../db'
import uuid from 'react-uuid'

const VerModalExcursion = ({ AgregarThour, state , ValidarFechaReducer}) => {
  let item = excursiones.filter((el) => el.id === state)
  const [fecha, setFecha] = useState('')

  return (
    <>
      {
        item.length > 0 &&
        <div className="modal" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header">
                <h4 className="modal-title text-danger" id="exampleModalLabel" >Excursiones al Amazonas</h4>
                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
              </div>
              <div className="modal-body">
                <div className="row">
                  <div className="col-md-6 ">
                    <img src={item[0].img} className="card-img-top" alt="..." />
                  </div>
                  <div className="col-md-6 ms-auto">
                    <p className='fs-5 text-start'>{item[0].description} </p>

                    <p className='fs-6 text-start'>INCLUYE: {item[0].plan}</p>


                    <div className="row g-3 align-items-center">
                      <div className="col-auto">
                        <label className="col-form-label"> Elige una fecha</label>
                      </div>
                      <div className="col-auto">
                        <input type="date"
                          name="fecha"
                          className="form-control"
                          autoComplete="off" className="form-control"
                           aria-describedby="passwordHelpInline"
                           value={fecha} 
                           onChange={(e) =>setFecha(e.target.value)}  />
                      </div>
                     
                    </div>
                <br />
                    <button className="btn btn-outline-info me-3 ">Preguntar</button>
                    <button className="btn btn-outline-success ">Compartir</button>
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-danger mx-auto btn-lg" data-bs-dismiss="modal"
                  onClick={() => {
                    if(fecha.trim() ===''){
                      ValidarFechaReducer(true)
                      return
                  }
                  ValidarFechaReducer(false)
                    AgregarThour({
                    nombre: item[0].title,
                    fecha,
                    factura: item[0].precio,
                    id: uuid(),
                  })}
                }
                >Comprar Tour </button>
              </div>
            </div>
          </div>
        </div>
      }
    </>
  )
}

export default VerModalExcursion
